.App {
  text-align: center;
  margin: 0 auto;
  width:96vmin;
  font-family: 'Work Sans', sans-serif;
  /* display: flex;
  flex-direction: column;
  align-items: center; */

}

.header {
  padding: 2.5vh 0;
  min-height: 10vh;
  vertical-align: middle;
  text-align:center;
  color: white;
  width:96vmin;
  margin: 0 auto;
}

.title {
  font-size: 6vh;
  font-weight: 600;
  margin: 0;
  text-shadow: 6px 6px 0px rgba(0,0,50,0.2) ;
}

.desc{
  margin:0;
  font-weight: 300;
  font-size: 3vh;
}

.container{
  border-radius: 10px;
  margin:0 auto;
  width:96vmin;
}

.pictures{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around
}


h1{
  font-size: 4vmin;
}

h2{
  font-size: 4vmin;
  font-weight: 300;
}

.mom h4, .dad h4{
  color:orangered;
  font-size: 6vmin;
  font-weight: 600;
  text-shadow: 7px 6px 8px rgba(0,0,10,0.2) ;
  margin:auto;
  -ms-flex-item-align: center;
      align-self: center; 
}

.child h4{
  vertical-align: middle;
  text-align:center;
  color:orangered;
  padding-top:0.35vh;
  font-size: 5vmin;
  font-weight: 300;
  text-shadow: 7px 6px 8px rgba(0,0,10,0.2) ;
  margin: auto;
}

h3{
  padding-top:1.5vmin;
  font-size: 4vmin;
  font-weight: 600;
  color:#517fa4;
  text-shadow: 7px 6px 5px rgba(0,0,10,0.2) ;
  clear:both;
  margin:auto;

}

p{
  margin:2px;
  color:lightgray;
  font-weight: 300;
  font-size: 1.8vmin;

}


#mom, #dad, #child{
  border-radius: 50%;
  -o-object-fit: contain;
     object-fit: contain;
  border: 2px solid gray;
  background:rgb(90,90,92);
  width: 26vmin;
  height: 26vmin;
  margin-top: 3vmin;
  left:0;
  -webkit-box-shadow:2px 4px 6px #888888;
  box-shadow:2px 4px 6px #888888;
}

.mom,.dad, .child{
  width: 32vmin;
margin:0 auto;
background-color: white;
border-radius: 15px;
}



